import React from "react"
import { graphql } from "gatsby"

import Bio from "../components/Bio"
import Layout from "../components/layout"
import Seo from "../components/Seo"
import { PostCard } from "../components/postCard"
// import ArchivePagination from "../components/ArchivePagination"

import { Stack, StackDivider } from "@chakra-ui/react"

const Archive = props => {
  const {
    data: {
      allWpPost: { nodes, pageInfo },
    },
    pageContext: { archiveType, archivePath },
  } = props
  const posts = nodes

  const schema = {
    "@context": "https://schema.org",
    "@graph": [
      {
        "@type": "Organization",
        "@id": "https://www.idrivesocal.com/#organization",
        name: "iDriveSoCal",
        url: "https://www.idrivesocal.com/",
        sameAs: [
          "https://www.facebook.com/idrivesocal/",
          "https://www.instagram.com/idrivesocal/",
          "https://www.linkedin.com/company/idrivesocal/",
          "https://www.youtube.com/channel/UC0HZusGNEBUl3lO7AdprFdQ",
          "https://www.pinterest.com/iDriveSoCal/",
          "https://twitter.com/idrivesocal",
        ],
        logo: {
          "@type": "ImageObject",
          "@id": "https://www.idrivesocal.com/#logo",
          inLanguage: "en-US",
          url:
            "https://www.idrivesocal.com/wp-content/uploads/2019/05/iDriveSoCal.jpg",
          contentUrl:
            "https://www.idrivesocal.com/wp-content/uploads/2019/05/iDriveSoCal.jpg",
          width: 824,
          height: 606,
          caption: "iDriveSoCal",
        },
        image: { "@id": "https://www.idrivesocal.com/#logo" },
      },
      {
        "@type": "WebSite",
        "@id": "https://www.idrivesocal.com/#website",
        url: "https://www.idrivesocal.com/",
        name: "iDriveSoCal",
        description: "Automotive News &amp; Events",
        publisher: { "@id": "https://www.idrivesocal.com/#organization" },
        potentialAction: [
          {
            "@type": "SearchAction",
            target: "https://www.idrivesocal.com/?s={search_term_string}",
            "query-input": "required name=search_term_string",
          },
        ],
        inLanguage: "en-US",
      },
      {
        "@type": "CollectionPage",
        "@id": "https://www.idrivesocal.com/#webpage",
        url: "https://www.idrivesocal.com/",
        name: "Automotive News &amp; Events &bull; iDriveSoCal",
        isPartOf: { "@id": "https://www.idrivesocal.com/#website" },
        description:
          "Vehicle reviews, car shows & events, car buying tips, and a network of transparent auto dealers.",
        breadcrumb: { "@id": "https://www.idrivesocal.com/#breadcrumb" },
        inLanguage: "en-US",
        potentialAction: [
          { "@type": "ReadAction", target: ["https://www.idrivesocal.com/"] },
        ],
      },
      {
        "@type": "BreadcrumbList",
        "@id": "https://www.idrivesocal.com/#breadcrumb",
        itemListElement: [
          {
            "@type": "ListItem",
            position: 1,
            item: {
              "@type": "WebPage",
              "@id": "https://www.idrivesocal.com/",
              url: "https://www.idrivesocal.com/",
              name: "Home",
            },
          },
        ],
      },
    ],
  }

  if (!posts.length) {
    return (
      <Layout isHomePage>
        <Seo title="Automotive News & Events" />
        <Bio />
        <p>
          No blog posts found. Add posts to your WordPress site and they'll
          appear here!
        </p>
      </Layout>
    )
  }

  return (
    <Layout isHomePage {...pageInfo} archivePath={archivePath}>
      <Seo title="Automotive News & Events" schemaMarkup={schema} />
      <Stack
        as="ol"
        divider={<StackDivider borderColor="gray.200" />}
        spacing={4}
        mb={4}
        p={8}
        listStyleType="none"
      >
        {posts.map((post, index) => {
          const title = post.title
          const featuredImage = {
            fluid:
              post.featuredImage?.node?.localFile?.childImageSharp
                ?.gatsbyImageData,
            alt: post.featuredImage?.node?.alt || ``,
          }

          return (
            <>
              <PostCard
                key={index}
                title={title}
                featuredImage={featuredImage}
                uri={post.uri}
                date={post.date}
                excerpt={post.excerpt}
                author={post.author.node}
                categories={post.categories.nodes}
              />
            </>
          )
        })}
      </Stack>
      {/* <Pagination>
            {previousPagePath && (
              <>
                <AppButton url={previousPagePath} text="< Previous"></AppButton>
                <br />
              </>
            )}
            {nextPagePath && (
              <AppButton url={nextPagePath} text="Next >"></AppButton>
            )}
          </Pagination> */}
      {/* <ArchivePagination {...pageInfo} archivePath={archivePath} /> */}
    </Layout>
  )
}

export default Archive

export const pageQuery = graphql`
  query WordPressPostArchive(
    $offset: Int!
    $perPage: Int!
    $userDatabaseId: Int
    $categoryDatabaseId: Int
  ) {
    allWpPost(
      sort: { fields: [date], order: DESC }
      limit: $perPage
      skip: $offset
      filter: {
        author: { node: { databaseId: { eq: $userDatabaseId } } }
        categories: {
          nodes: { elemMatch: { databaseId: { eq: $categoryDatabaseId } } }
        }
      }
    ) {
      nodes {
        excerpt
        uri
        date(formatString: "MMMM DD, YYYY")
        title
        excerpt
        featuredImage {
          node {
            altText
            localFile {
              childImageSharp {
                gatsbyImageData(placeholder: TRACED_SVG, layout: FULL_WIDTH)
              }
            }
          }
        }
        author {
          node {
            name
            uri
          }
        }
        categories {
          nodes {
            name
            uri
          }
        }
      }
      pageInfo {
        hasNextPage
        hasPreviousPage
        currentPage
        pageCount
      }
    }
  }
`
