import React from "react"
import { Link as GatsbyLink } from "gatsby"
import parse from "html-react-parser"
import { AppButton } from "./atoms/button"
import { Stack, Heading, Text, Link } from "@chakra-ui/react"

import { PostLiContent, PostListMeta } from "../styles/contentStyles"

import { ChakraImage } from "./atoms/chakraImage"

export const PostCard = props => {
  return (
    <>
      <li key={props.uri}>
        <Stack
          direction={["column", "column", "row", "row"]}
          className="post-list-item"
          itemScope
          itemType="http://schema.org/Article"
        >
          <ChakraImage
            image={props.featuredImage.fluid}
            alt={props.featuredImage.alt}
            minWidth={["100%", "50%"]}
            objectFit="cover"
            maxHeight="16rem"
          />
          <PostLiContent
            width={{base:"100%", md: "50%"}}
          >
            <header>
              <Heading as="h3" size="md" color="brand.600">
                <Link
                  as={GatsbyLink}
                  to={props.uri}
                  itemProp="url"
                  color="brand.600"
                >
                  <span itemProp="headline">{parse(props.title)}</span>
                </Link>
              </Heading>
              <PostListMeta
                direction="row"
                color="gray.500"
                spacing="4px"
                pt={2}
                _hover={{ color: "gray.600" }}
              >
                <Text as="small" size="sm">
                  by
                  <Link as={GatsbyLink} to={props.author.uri} itemProp="url">
                    {" "}
                    {props.author.name}
                  </Link>
                </Text>
                {props.categories && (
                  <Text as="small" size="sm">
                    |
                  </Text>
                )}
                {props.categories &&
                  props.categories.map((cat, index, cats) => {
                    return (
                      <Text as="small" size="sm">
                        <Link as={GatsbyLink} to={cat.uri} itemProp="url">
                          {cat.name}
                        </Link>
                        {index < cats.length - 1 && ","}
                      </Text>
                    )
                  })}
              </PostListMeta>
            </header>
            <Text
              as="section"
              fontSize="sm"
              isTruncated
              whiteSpace="normal"
              noOfLines={4}
              itemProp="description"
            >
              {parse(props.excerpt)}
            </Text>
            <AppButton url={props.uri} text="Read More" />
          </PostLiContent>
        </Stack>
      </li>
    </>
  )
}
