import React from "react"
import { Link as GatsbyLink } from "gatsby"
import { Link } from "@chakra-ui/react"

export const AppButton = props => {
  return (
    <Link 
      as={GatsbyLink}
      to={props.url}
      bgColor="gray.200"
      color="brand.600"
      py={3}
      px={2}
      borderRadius="md"
      textTransform="uppercase"
      width="fit-content"
      _hover={{ bgColor: 'brand.500', color: "gray.100" }}
    >
      {props.text}
    </Link>)
}
